.header {
    width: 100%;
    height: 160px;
    background: deepskyblue;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.h_top {
    width: 1500px;
    height: 110px;
    background: darkkhaki;
}

.h_nav {
    width: 1500px;
    height: 50px;
    background: darkgreen;
    padding: 10px 0 0 50px;
    box-sizing: border-box;
}

.h_nav ul {

}

.h_nav ul li {
    float: left;
    margin: 0 30px 0 0;
}

.h_nav ul li a {
    font-size: 20px;
    color: #fff;
}

@media ( max-width:1500px ){
    .h_nav {
        width: 90%
    }

    .h_top {
        width: 90%
    }
}