
.contents {
    width: 100%;
    height: 3000px;
    background: deeppink;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.con1 {
    width: 1500px;
    height: 200px;
    background: darkgoldenrod;
    margin: 150px 0 0 0;
}

.con {
    width: 1500px;
    height: 200px;
    margin: 30px 0 0 0;
}

.con2 {
    background: darkblue;
}

.con3 {
    background: darkolivegreen;
}

.con4 {
